#trump-board {
    display: flex;
    align-items: center;
    justify-content: space-around;
    height: 55px;
    width: 60px;
    background: white;
    position: absolute;
    top: 10px;
    right: 10px;
    box-shadow: 0px 0px 3px 0px rgba(62, 59, 59, 0.5);
    border-radius: 2px;
}

#trump-board img {
    height: 43px;
    object-fit: cover;
    overflow: hidden;
}