#scoreboard {
    display: flex;
    align-items: center;
    justify-content: space-around;
    height: 55px;
    width: 80px;
    background: linear-gradient(180deg,#f7f7f7 39.58%,#dce1ed 100%);
    position: absolute;
    top: 10px;
    left: 10px;
    box-shadow: 0px 0px 3px 0px rgba(62, 59, 59, 0.5);
    border-radius: 2px;
}

#scoreboard .player-score, #scoreboard .enemy-score {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    gap: 5px;
}

#scoreboard .player-score div{
    height: 20px;
    width: 20px;
    background-color: blue;
}

#scoreboard .enemy-score div {
    height: 20px;
    width: 20px;
    background-color: red;
}